#record-vue{
  height: 100%;
  width: 100%;
  background: black;
  position:absolute;
  top:0px;
  left:0px;
  color: white;
}

#record-vue #record-logo {
  position: fixed;
  width: 100%;
  bottom: 30%;
  text-align: center;
}

#record-vue #record-logo #record-logo-text {
  position: fixed;
  width: 30%;
  right: 20%;
  bottom: 60%;
  font: 2em "Open Sans", Roboto;
  color: #ED9F9F;
}

#record-vue #record-overlay {
  width: 70%;
  position: fixed;
  bottom: 15%;
  height: 10%;
  padding: 5px 15% 5px 15%;
  text-align: center;
  font: 2em "Open Sans", Roboto;
}
